const storageKey = "preferred-theme";

const applyTheme = (theme) => {
  const root = document.documentElement;
  if (theme === "dark") {
    root.classList.add("dark");
  } else {
    root.classList.remove("dark");
  }
};

const resolveInitialTheme = () => {
  const persistedPreference = localStorage.getItem(storageKey);
  if (persistedPreference === "light" || persistedPreference === "dark") {
    return persistedPreference;
  }

  const prefersDark = window.matchMedia("(prefers-color-scheme: dark)").matches;
  return prefersDark ? "dark" : "light";
};

const setTheme = (theme) => {
  localStorage.setItem(storageKey, theme);
  applyTheme(theme);
  const toggleButton = document.querySelector("[data-theme-toggle]");
  if (toggleButton) {
    toggleButton.setAttribute(
      "aria-pressed",
      theme === "dark" ? "true" : "false"
    );
    toggleButton.setAttribute(
      "aria-label",
      theme === "dark" ? "Activate light theme" : "Activate dark theme"
    );
    const icon = toggleButton.querySelector("span[data-theme-icon]");
    if (icon) {
      icon.textContent = theme === "dark" ? "ðŸŒ™" : "â˜€ï¸";
    }
  }
};

document.addEventListener("DOMContentLoaded", () => {
  const theme = resolveInitialTheme();
  applyTheme(theme);

  const toggleButton = document.querySelector("[data-theme-toggle]");
  if (toggleButton) {
    toggleButton.addEventListener("click", () => {
      const nextTheme = document.documentElement.classList.contains("dark")
        ? "light"
        : "dark";
      setTheme(nextTheme);
    });
  }

  const yearTarget = document.querySelector("[data-year]");
  if (yearTarget) {
    yearTarget.textContent = new Date().getFullYear();
  }
});
